import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController } from 'ionic-angular';
import { Http } from '@angular/http';
import 'rxjs/add/operator/map';

/**
 * Generated class for the VehiclePartsSelectPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-vehicle-parts-select',
  templateUrl: 'vehicle-parts-select.html',
})
export class VehiclePartsSelectPage {
  information:any;
  selectedType={};
  constructor(public navCtrl: NavController, public navParams: NavParams,public http:Http,public viewCtrl: ViewController) {
    let localData = http.get('../../assets/json/vehicleParts.json').map(res => res.json());
    localData.subscribe(data => {
      this.information = data;
      console.log(this.information[0].vehiclePart);
    })
  }

  toggleSection(i) {
    this.information[i].open = !this.information[i].open;
  }
  ionViewDidLoad() {
    console.log('ionViewDidLoad VehiclePartsSelectPage');
  }
  selectType(logo) {
    this.selectedType=logo;
    this.viewCtrl.dismiss(this.selectedType);
  }
}



